img {
  display: block;
  margin: auto;
  margin-top: 2rem;
  height: 20rem;
  width: 20rem;
}

.username-profile {
  text-align: center;
  padding-top: 3rem;
  font-size: 3rem;
  font-weight: 100;
}

.ttpost {
  text-align: center;
  font-size: 2rem;
}

#profile {
  position: fixed;
  bottom: 8rem;
  right: 8rem;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: none;
  color: rgb(150, 147, 147);
  background: linear-gradient(145deg, #1c1d21, #17181c);
  box-shadow: 6px 6px 8px #101114, 2px -1px 15px 2px #ffffff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
